@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin hover($property, $toValue) {
  transition: #{$property} 0.3s;
  &:hover {
    #{$property}: $toValue;
  }
}

@mixin content-padding-inline {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 72px;
  }

  @include on-desktop {
    padding-inline: 120px;
  }
}

.container {
  @include content-padding-inline;

  &--main,
  &--footer {
    @include on-desktop {
      max-width: 1020px;
      margin: 0 auto;
    }
  }

  &--about {
    padding: 48px 20px;

    @include on-tablet {
      padding: 120px 72px;
    }

    @include on-desktop {
      padding: 120px;
    }
  }

  &--header,
  &--menu {
    @include on-desktop {
      max-width: 1020px;
      margin: 0 auto;
    }
  }
}

@mixin page-grid {
  --columns: 2;

  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet {
    --columns: 6;

    column-gap: 24px;
  }

  @include on-desktop {
    --columns: 12;
  }
}
